.reviews {
    width: 1440px;
    color: #fff;
    margin: 100px auto;
    padding: 0 10px;
    &_title {
        text-align: center;
    }
    &_page {
        width: 600px;
        margin-left: -5px;
        background-color: #343942;
        border-radius: 5px;
        transition: 4s ease;
    }
    &_first {
        margin-left: 0;
    }
    &_subtitle {
        // width: 70px;
        span {
            border-top: 2px solid #E1B168;
            border-bottom: 2px solid #E1B168;
            padding: 4px 0;
            text-transform: uppercase;
            font-family: 'Josefin Sans';
            font-style: normal;
            font-weight: 400;
            font-size: 15px;
            line-height: 22px;
            letter-spacing: 3px;
        }
    }
    &_descr {
        font-family: 'Josefin Sans';
        font-style: normal;
        font-weight: 300;
        font-size: 20px;
        line-height: 30px;
        letter-spacing: 2px;
    }
    h2 {
        font-family: 'Cormorant Infant';
        font-style: normal;
        font-weight: 400;
        font-size: 40px;
        line-height: 56px;
        letter-spacing: 2px;
        margin: 0;
        margin-top: 20px;
    }
    &_line {
        width: 80%;
        height: 2px;
        background-color: #797E89;
        margin: 30px auto;
    }
    &_user {
        display: flex;
        padding-left: 40px;
        height: 120px;
        align-items: center;
        // margin-top: 40px;
        h3 {
            font-family: 'Cormorant Infant';
            font-style: normal;
            font-weight: 400;
            font-size: 30px;
            line-height: 42px;
            letter-spacing: 2px;
            margin: 0;
        }
        span {
            color: #E1B168;
            font-family: 'Josefin Sans';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 30px;
            letter-spacing: 0.2px;
        }
        img {
            margin-right: 30px;
            border-radius: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 107px;
            height: 114px;
        }
    }
    &_descr {
        text-align: center;
        padding: 5px 10px;
    }
    &_bg {
        margin-top: 40px;
        opacity: .5;
        text-align: center;
        position: relative;
        img {
            width: 95%;
            height: 600px;
        }
    }
    &_welcome {
        position: absolute;
        top: 35%;
        transform: translateY(-50%);
        left: 50%;
        transform: translateX(-50%);
        h1 {
            font-size: 72px;
            letter-spacing: 4px;
        }
    }
}


@media (max-width: 1200px) {
    .reviews {
        width: 1200px;
        padding: 0;
        &_title {
            text-align: center;
        }
    }
}

@media (max-width: 991px) {
    .reviews {
        width: 991px;
        margin-left: 0;
        &_bg {
            img {
                width: 90%;
                height: 570px;
            }
        }
    }
}

@media (max-width: 768px) {
    .reviews {
        width: 768px;
        h2 {
            font-size: 30px;
            line-height: 44px;
        }
        &_subtitle {
            span {
                font-size: 14px;
            }
        }
        &_descr {
            font-size: 17px;
        }
        &_bg {
            img {
                width: 90%;
                height: 500px;
            }
        }
        &_welcome {
            h1 {
                font-size: 42px;
            }
        }
    }
}

@media (max-width: 575px) {
    .reviews {
        width: 575px;
        &_line {
            display: none;
        }
        &_user {
            img {
                width: 90px;
                height: 90px;
            }
        }
        &_descr {
            width: 370px;
            margin-top: 35px;
            padding: 5px 15px;
        }
        &_bg {
            display: none;
        }
        &_title {
            width: 500px;
            margin: 0 auto;
        }
    }
}